export default {
    data() {
        return {
            value: 1,    // 默认选中昨天
            startTime: null,  //  自定义时间
            dataTime: null,   //  筛选时间
            show: false,   // 筛选时间的弹窗显示隐藏
            rankingList: [
                { top:6,head:require("@/components/manage/images/head.jpg"),run:"15825",name:"我",num:"9527",dianzan:105 },
                { top:1,head:require("@/components/manage/images/head.jpg"),run:"15825",name:"陈丽丽",num:"9527",dianzan:105 },
                { top:2,head:require("@/components/manage/images/head.jpg"),run:"15825",name:"陈丽丽",num:"9527",dianzan:105 },
                { top:3,head:require("@/components/manage/images/head.jpg"),run:"15825",name:"陈丽丽",num:"9527",dianzan:105 },
                { top:4,head:require("@/components/manage/images/head.jpg"),run:"15825",name:"陈丽丽",num:"9527",dianzan:105 }
            ],
        }
    },
    methods:{
        navRigth(){
            console.log("我可能要分享了")
        },
        date(index){
            if(index == 0){
                this.dataTime = this.common.commonFun.get_date(0)
            }
            if(index == 1){
                this.dataTime = this.common.commonFun.get_date(1)
            }
            if(index == 2){
                this.show = !this.show;
            }
            console.log(this.dataTime)
        },
        onConfirm(){
            console.log('我确定了')
        }
    }
}



